import appOrderGoodsInfo from '../../../components/page-component/app-order-goods-info/app-order-goods-info.vue';
import appUploadImage from "../../../components/basic-component/app-upload-image/app-upload-image.vue";
import appOrderBanner from '../../../components/page-component/app-order-banner/app-order-banner.vue';
import appSelect from "../components/app-select";

export default {
    components: {
        appOrderGoodsInfo,
        appUploadImage,
        appOrderBanner,
        'app-select': appSelect,
    },
    data() {
        return {
            orderDetail: {},
            form: {
                image_list: [],
                remark: '',
                mobile: '',
                cause: '',
            },
            maxNum: 6,
            cause: {
                list: [],
                visible: false,
                index: 1,
            },
            isShow: false,
        }
    },
    methods: {
        getOrderDetail() {
            let self = this;
            self.$showLoading();
            self.$request({
                url: self.$api.order.detail,
                data: {
                    id: self.order_id,
                }
            }).then(response => {
                self.$hideLoading();
                self.isShow = true;
                if (response.code === 0) {
                    self.orderDetail = response.data.detail;
                    self.setPageTitle();
                } else {
                    uni.showModal({
                        title: '',
                        content: response.msg,
                        showCancel: false,
                    });
                }
            }).catch(() => {
                self.$hideLoading();
            });
        },
        formSubmit() {
            let self = this;
            if (!self.form.cause) {
                uni.showModal({
                    title: '',
                    content: '请选择退款原因',
                    showCancel: false,
                });
                return;
            }
            uni.showModal({
                title: '提示',
                content: '确认申请退款？',
                success: function(res) {
                    if (res.confirm) {
                        self.$subscribe(self.orderDetail.template_message_list).then(response => {
                            self.cancelAction();
                        }).catch(res => {
                            self.cancelAction();
                        });
                    }
                }
            });
        },
        imageEvent(e) {
            this.form.image_list = e.imageList;
        },
        bindPickerChange(e) {
            if (!e.is_modal_confirm) {
                this.cause.index = e.index;
                this.form.cause = this.cause.list[this.cause.index];
            }
            this.cause.visible = false;
        },
        cancelAction() {
            let self = this;
            uni.showLoading({ title: '取消中' });
            self.$request({
                url: self.$api.order.cancel,
                data: {
                    id: self.orderDetail.id,
                    cancel_data: JSON.stringify(self.form)
                }
            }).then(response => {
                uni.hideLoading();
                if (response.code === 0) {
                    if (self.sign === 'advance') {
                        self.$request({
                            url: self.$api.advance.order_detail,
                            method: 'get',
                            data: {
                                id: self.order_id,
                            }
                        }).then(response => {
                            if (response.code === 0) {
                                self.orderDetail = response.data.detail;
                            } else {
                                uni.showModal({
                                    title: '',
                                    content: response.msg,
                                    showCancel: false,
                                });
                            }
                        })
                    } else {
                        self.getOrderDetail();
                    }
                } else {
                    uni.showModal({
                        title: '',
                        content: response.msg,
                        showCancel: false,
                    });
                }
            }).catch(() => {
                uni.hideLoading();
            });
        },
        cancelCauseList() {
            let self = this;
            self.$request({
                url: self.$api.order.cancel_cause_list,
            }).then(response => {
                if (response.code === 0) {
                    self.cause.list = response.data.list;
                } else {
                    uni.showModal({
                        title: '',
                        content: response.msg,
                        showCancel: false,
                    });
                }
            }).catch(() => {
                self.$hideLoading();
            });
        },
        cancelApply() {
            let self = this;
            uni.showLoading({ title: '取消中' });
            self.$request({
                url: self.$api.order.cancel_apply,
                method: "post",
                data: {
                    id: self.order_id
                }
            }).then(response => {
                uni.hideLoading();
                if (response.code === 0) {
                    uni.navigateBack();
                } else {
                    uni.showModal({
                        title: '',
                        content: response.msg,
                        showCancel: false,
                    });
                }
            }).catch(() => {
                uni.hideLoading();
            });
        },
        // 设置页面标题
        setPageTitle() {
            let title = '申请退款';
            if (this.orderDetail.cancel_status == 2) {
                title = '订单退款'
            }

            uni.setNavigationBarTitle({
                title: title
            });
        },
        // 图片预览
        previewImage(index) {
            let imageList = this.orderDetail.cancel_data.image_list;
            uni.previewImage({
                current: imageList[index],
                urls: imageList
            })
        },
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.order_id = options.id;
        this.getOrderDetail();
        this.cancelCauseList();
    }
}
